<template>
    <a-modal
            width="60%"
            v-model="visible"
            :footer="null"
            :destroyOnClose="true"
            @afterClose="handleClose"
            @cancel="handleClose"
    >
        <div slot="title">
            <a-row>
                <a-col :span="1">
                    <img :src="require('@/assets/pc/images/dhico.png')"
                         style="z-index: 9999;height: 18px">
                </a-col>
                <a-col :span="23">
                    {{title}}
                </a-col>
            </a-row>
        </div>

        <a-spin :spinning="spinning">
            <a-list
                    :split="false"
                    :dataSource="data"
            >
                <a-list-item slot="renderItem"
                             slot-scope="item, index">
                    <span style="color: #c12625;font-weight:bold;padding-right: 5px">{{item.title}} :</span>{{item.info}}
                </a-list-item>
            </a-list>
        </a-spin>


    </a-modal>
</template>

<script>
    import {getMeeting} from "@/api/meeting";

    export default {
        props: {
            id: {required: true, type: Number},
            visible: {required: true, type: Boolean},
        },
        model: {
            prop: 'visible',
            event: 'change'
        },
        data() {
            return {
                spinning: true,
                title: '数据加载中。。',
                confirmLoading: false,
                data: [],
            }
        },
        methods: {
            fetch() {
                this.spinning = true;
                if (this.id === 0) {
                    return;
                }

                getMeeting(this.id)
                    .then(res => {
                        this.title = res.data.title;
                        this.data = [
                            {title: '会议时间', info: res.data.opened_at_format},
                            {title: '会议地点', info: res.data.location},
                            {title: '会议类型', info: res.data.type_format},
                            {title: '会议发起人', info: res.data.initiate_user.name},
                            {title: '审核人', info: res.data.audit.audit_user.name},
                            {title: '参会人员', info: res.data.meeting_attend_user_name_format},
                            {title: '参会领导', info: res.data.meeting_leader_name_format},
                        ];
                        this.spinning = false;
                    })
            },
            handleClose() {
                this.$emit('change', false);
            },
        },
        watch: {
            visible(newVal, oldVal) {
                if (newVal) {
                    this.fetch();
                } else {
                    this.title = '数据加载中。。';
                    this.data = [];
                }
            }
        }
    }
</script>

